import TopAppBar from "../components/TopAppBar"
import { Stack, Box, LinearProgress, Button, Container } from "@mui/material"
import { SearchBar } from "../components/SearchBar"
import { CouponCard } from "../components/CouponCard"
import { useEffect, useState } from "react"
import { useNavigate } from "react-router-dom";
import axios from "axios"

export function Home() {
    const navigate = useNavigate();



    const config = {
        headers: {
            Authorization: localStorage.getItem('token'),
        }
    }
    const [userName, SetUserName] = useState('');
    const [totalCoupons, SetTotalCoupons] = useState('');
    const [totalFeedbacks, SetTotalFeedbacks] = useState('');
    const [coupons, SetCoupons] = useState([]);

    const couponStats = [
        {
            "img": "./assets/cwon.png", "text": "Coupons won", "num": totalCoupons.toLocaleString('en-US', {
                minimumIntegerDigits: 2,
                useGrouping: false
            })
        },
        {
            "img": "./assets/fbgiven.png", "text": "Feedbacks Given", "num": totalFeedbacks.toLocaleString('en-US', {
                minimumIntegerDigits: 2,
                useGrouping: false
            })
        },

    ]
    useEffect(() => {

        if (localStorage.getItem('token')) {

            axios.post('https://api.extraa.in/public/api/user-coupon-details', '', config).then(response => {
                console.log(response.data.Data);
                SetTotalCoupons(response.data.Data.length)
                SetCoupons(response.data.Data);
            })

            axios.post('https://api.extraa.in/public/api/user-feedback-details', '', config).then(response => {

                SetTotalFeedbacks(response.data.Data.length)
                // SetCoupons(response.data.Data);
            })

            axios.post('https://api.extraa.in/public/api/get-user-profile', '', config).then(response => {
                console.log(response.data.Data)


            })
        } else {
            navigate('/');
        }
        SetUserName(localStorage.getItem('user_name'));
    }, []);


    return (
        <div className="home">
            <TopAppBar home={true} />
            {/* -------------------------- Welcome Section -------------------------- */}

            <Stack justifyContent={'center'} alignItems='center' pt={6}>
                <h3 style={{ color: '#4F3084', fontSize: '1.6em', margin: 0, paddingLeft: '1em', paddingRight: '1em', textAlign: 'center' }}>
                    Welcome! {userName}
                </h3>

                {/* -------------------------- Stats -------------------------- */}

                <Stack justifyContent={'center'} alignItems='center' pt={4} direction='row' gap={4} pb={4}>
                    {/* <Card  style={{borderRadius:24}}> */}
                    {couponStats.map(item => (
                        <Stack alignItems='center' justifyContent='space-between' sx={{ textAlign: 'center' }} key={item.text}>
                            <img src={item.img} alt="" />
                            <h3 style={{}}> {item.text}</h3>
                            <h2 style={{ margin: 0, color: '#4F3084', lineHeight: 0, fontFamily: 'rota-black' }}>{item.num}</h2>
                        </Stack>
                    ))}

                    {/* </Card> */}
                </Stack>
            </Stack>

            {/* -------------------------- Complete your profile -------------------------- */}

            <Stack style={{ background: '#4F3084', color: 'white' }} p={4} alignItems='center'>
                <p> Complete your profile to get better coupons</p>
                <p style={{ textAlign: 'right', width: '100%', maxWidth: '800px', margin: 0, lineHeight: 1 }}> 1/3</p>

                <Box sx={{ width: '100%', maxWidth: '800px' }} mb={4}>
                    <LinearProgress variant="determinate" value={20} color='secondary' />
                </Box>

                <Button variant="contained" className="y-btn" onClick={() => navigate('/user-profile')} >Go to Profile</Button>
            </Stack>

            {/* -------------------------- Search-------------------------- */}

            <Stack alignItems='center' mt={2}>
                <SearchBar></SearchBar>
            </Stack>

            {/* -------------------------- Filter by industries -------------------------- */}

            <Stack direction='row'>
                {/* industries */}
            </Stack>

            {/* --------------------------  Coupons  -------------------------- */}

            <Stack direction='row' justifyContent='center' >
                <Stack mt={8} mb={8} direction={{ 'sm': 'column', 'lg': 'row' }} gap={4} justifyContent='center' flexWrap='wrap' sx={{ maxWidth: 1200 }}>

                    {coupons.map(obj => (
                        <CouponCard
                            key={obj.coupon_id}
                            brandName={obj.brand.brand_name.toLowerCase()}
                            offerType={obj.coupon.offer_details.slice(0, 8)}
                            offerDetails={obj.coupon.offer_details.slice(8).toLowerCase()}
                            code={obj.coupon.coupon_code}
                            validity={obj.coupon.expiry_date}
                            brandLogo={'https://extraa.in/admin/web/uploads/retailer/' + obj.brand.brand_logo}
                            industry_name={obj.coupon.industry_name.toLowerCase()}
                            industry_icon={'https://extraa.in/admin/web/uploads/industry/' + obj.coupon.industry_logo}
                            terms={obj.coupon.terms_and_conditions}
                            coupon_color= {obj.coupon.color_code}
                        />
                    ))}


                </Stack>
            </Stack>

        </div>
    )
}